import React, { useEffect, useState } from 'react';

const sliderImages = [
  process.env.PUBLIC_URL + '/images/1000278926.jpg',
  process.env.PUBLIC_URL + '/images/1000278927.jpg',
  process.env.PUBLIC_URL + '/images/1000278928.jpg',
  process.env.PUBLIC_URL + '/images/1000278929.jpg',
];

const ProductList = ({ onAddToCart }) => {
  const [current, setCurrent] = useState(0);
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrent(prev => (prev + 1) % sliderImages.length);
    }, 2500);
    return () => clearInterval(interval);
  }, []);

  if (loading) return <p>Loading products...</p>;

  const product = products[current] || {};

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', margin: '2rem 0' }}>
      <div style={{ position: 'relative', width: 320, height: 320, overflow: 'hidden', borderRadius: 18, boxShadow: '0 4px 24px rgba(0,0,0,0.08)', marginBottom: 24 }}>
        <img
          src={sliderImages[current]}
          alt={product.title || 'Product'}
          style={{ width: '100%', height: '100%', objectFit: 'cover', borderRadius: 18, transition: 'all 0.5s' }}
        />
        <div style={{ position: 'absolute', bottom: 0, left: 0, width: '100%', background: 'rgba(0,0,0,0.5)', color: '#fff', padding: '12px 0', textAlign: 'center', fontWeight: 700, fontSize: 20, borderBottomLeftRadius: 18, borderBottomRightRadius: 18 }}>
          {product.title}
        </div>
      </div>
      <button onClick={() => onAddToCart(product)} style={{ background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)', color: '#fff', border: 'none', borderRadius: 24, padding: '12px 36px', fontSize: 18, fontWeight: 700, boxShadow: '0 2px 8px rgba(0,123,255,0.08)', cursor: 'pointer' }}>
        Add to Cart
      </button>
    </div>
  );
};

export default ProductList;